.profile{color:white;text-align:center}.profile .username{margin-bottom:5px}.profile .email{color:rgba(255,255,255,0.8);margin-top:5px}.profile .logout{background:none;border:1px solid #0e927d;border-radius:20px;color:#0e927d;padding:5px 8px;font-family:'Work Sans', sans-serif;font-size:0.75em;font-weight:700}.profile .logout:hover{border:1px solid #e5674e;color:#e5674e;cursor:pointer}.profile .order-history{text-align:left;padding:20px}.profile .order-history .order-container{font-size:0.75em;color:rgba(255,255,255,0.801);display:grid;grid-template-areas:'orderno date' 'total price' 'line line'}.profile .order-history .order-container>p{margin:5px}.profile .order-history .order-container .orderno{grid-area:orderno;font-weight:700}.profile .order-history .order-container .date{grid-area:date;justify-self:end}.profile .order-history .order-container .total-text{grid-area:total}.profile .order-history .order-container .price{grid-area:price;justify-self:end}.profile .order-history .order-container .line{grid-area:line;border-bottom:1px solid rgba(255,255,255,0.322)}.profile .order-history .discounts{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;font-size:0.75em;font-weight:700;color:#e5674e;margin-top:10px}.profile .order-history .total{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;font-weight:700;color:rgba(255,255,255,0.8)}.profile .whoops{width:100%;height:350px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;text-align:center}.profile .whoops>h3{width:70%}.profile .loading>h1{opacity:0.7}

