.menu{color:#2f2926;min-height:744px}.menu .menu-title{text-align:center}.menu .menu-container{display:grid;padding:0px 16px 0px 16px;grid-auto-rows:minmax(16px, auto);grid-auto-columns:50px auto auto;grid-template-areas:'add coffee coffee-price' 'add desc desc'}.menu .add{grid-area:add;background:none;border:none;font-size:2em;align-self:center;justify-self:start;margin:0;padding:0 20px 0 0}.menu .add:hover{cursor:pointer}.menu .coffee{grid-area:coffee;margin:0;align-self:end;justify-self:start}.menu .coffee-price{grid-area:coffee-price;align-self:end;justify-self:end;margin-bottom:0}.menu .desc{grid-area:desc;font-size:0.75em;margin-top:5px;align-self:start;justify-self:start}.menu .whoops{width:100%;height:350px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;text-align:center}.menu .whoops>h3{width:70%}.menu .footer{position:absolute;bottom:0}.hide{-webkit-filter:blur(5px);filter:blur(5px)}.loading{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.loading>h2{opacity:0.7}

.cart .circle2{position:absolute;top:0px;right:5px}.cart .cart-icon{position:absolute;top:-4px;right:29px}.cart .cart-icon:hover{cursor:pointer}.cart .cart-card{width:340px;height:560px;background-color:rgba(255,255,255,0.93);position:absolute;top:75px;right:17px;border-radius:5px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;overflow-y:scroll;color:#2f2926;z-index:1;box-shadow:0px 0px 32px 0px #00000099;-webkit-animation:growDown 300ms ease-in-out forwards;animation:growDown 300ms ease-in-out forwards;-webkit-transform-origin:top center;transform-origin:top center}@-webkit-keyframes growDown{0%{-webkit-transform:scaleY(0);transform:scaleY(0)}80%{-webkit-transform:scaleY(1.1);transform:scaleY(1.1)}100%{-webkit-transform:scaleY(1);transform:scaleY(1)}}@keyframes growDown{0%{-webkit-transform:scaleY(0);transform:scaleY(0)}80%{-webkit-transform:scaleY(1.1);transform:scaleY(1.1)}100%{-webkit-transform:scaleY(1);transform:scaleY(1)}}.cart .cart-card .cart-title{text-align:center}.cart .cart-card .cart-item{padding:0px 16px 0 16px;margin-top:10px;display:grid;grid-auto-rows:20px 30px 20px;grid-auto-columns:auto auto 36px;grid-template-areas:'increase increase increase' 'title remove quantity' 'price price decrease'}.cart .cart-card .cart-item .increase{grid-area:increase;justify-self:end;border:none;background:none;font-size:1.2em;padding:0px 8px 0px 8px}.cart .cart-card .cart-item .cart-item-title{grid-area:title;align-self:center;margin:0}.cart .cart-card .cart-item .cart-item-quantity{grid-area:quantity;justify-self:center;align-self:center;font-weight:700}.cart .cart-card .cart-item .cart-price{grid-area:price;margin:0;font-size:0.75em}.cart .cart-card .cart-item .decrease{grid-area:decrease;justify-self:end;border:none;background:none;font-size:1.2em;padding:0px 8px 0px 8px}.cart .cart-card .cart-item .remove{grid-area:remove;justify-self:end;border:none;background:none;font-size:1.2em;align-self:center;margin-top:5px}.cart .cart-card .cart-total{padding:10px;margin-top:16px;display:grid;grid-template-areas:'discount discamount' 'total totalamount' 'including including'}.cart .cart-card .cart-total .c-discount{grid-area:discount;align-self:center;margin:0;font-size:0.8em;color:#e5674e}.cart .cart-card .cart-total .c-discamount{grid-area:discamount;margin:0px 10px;justify-self:end;font-size:0.8em;color:#e5674e}.cart .cart-card .cart-total .c-total{grid-area:total;margin:10px 0px 0px 0px}.cart .cart-card .cart-total .c-amount{grid-area:totalamount;margin:0;justify-self:end;margin:10px 0px 0px 0px}.cart .cart-card .cart-total .including{grid-area:including;margin:0px 0px 16px 0px;align-self:top;font-size:0.7em}.cart .cart-card .pay{text-align:center}.cart .cart-card .pay .pay-button{font-family:'PT Serif', serif;font-size:1.5em;font-weight:700;border:none;border-radius:50px;padding:10px;background-color:#2f2926;color:white;width:80%;margin-bottom:30px}.cart .cart-card .pay .pay-button:hover{background-color:#0e927d}.cart .pay-now{width:350px;height:744px;background-color:#2f2926;color:rgba(255,255,255,0.904);position:absolute;z-index:1;top:16px;right:12.5px;border-radius:5px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;text-align:center;box-shadow:0px 0px 32px 0px #00000099;-webkit-animation:rotateDiv 400ms ease-in-out forwards;animation:rotateDiv 400ms ease-in-out forwards;-webkit-transform-origin:top center;transform-origin:top center}@-webkit-keyframes rotateDiv{0%{-webkit-transform:rotateY(90deg);transform:rotateY(90deg)}80%{-webkit-transform:rotateY(-20deg);transform:rotateY(-20deg)}100%{-webkit-transform:rotateY(0);transform:rotateY(0)}}@keyframes rotateDiv{0%{-webkit-transform:rotateY(90deg);transform:rotateY(90deg)}80%{-webkit-transform:rotateY(-20deg);transform:rotateY(-20deg)}100%{-webkit-transform:rotateY(0);transform:rotateY(0)}}.cart .go{bo