.order-status{width:375px;min-height:690px;background-color:#e5674e;position:absolute;z-index:99;top:0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly;text-align:center;color:white}.order-status .main-content{height:600px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly}.order-status .main-content .top .orderNumber{color:rgba(255,255,255,0.7)}.order-status .main-content .middle{width:80%}.order-status .main-content .cool{border:none;font-family:'PT Serif', serif;font-size:1.2em;font-weight:700;border-radius:50px;padding:12px;width:36%;background-color:white}.order-status .main-content .cool:hover{background-color:#f3e4e1;cursor:pointer}.order-status .whoops{width:100%;height:350px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;text-align:center}.order-status .whoops>h3{width:70%}.order-status .whoops .cool{border:none;font-family:'PT Serif', serif;font-size:1.2em;font-weight:700;border-radius:50px;padding:12px;margin-top:20px;width:36%;background-color:white}.order-status .whoops .cool:hover{background-color:#f3e4e1;cursor:pointer}

